    width: 100%;
    height: 100%;

    display: flex;
    justify-content: center;
    align-items: center;
}

.login_inner_fir {
    width: 35%;
    height: 100%;

    display: flex;
    flex-direction: column;
    justify-content: space-between;

    border: 1px solid black;
    margin-right: 10px;
}

